import { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import React from 'react';

import './timerCount.css';
import {
  addHistoryPomodoro,
  addHistoryLongBreak,
  addHistoryShortBreak,
} from '../../../redux/actionCreators';

const TimerCount = ({ settings, mode }) => {
  const dispatch = useDispatch();
  let timeMode;

  switch (mode) {
    case 'pomodoro':
      timeMode = settings.pomodoro.time;
      break;
    case 'shortBreak':
      timeMode = settings.shortBreak.time;
      break;
    case 'longBreak':
      timeMode = settings.longBreak.time;
      break;
    default:
      timeMode = '';
  }

  let [count, setCount] = useState(timeMode * 60);
  let [timerActive, settimerActive] = useState(false);

  const onToggleStart = () => {
    settimerActive((state) => {
      return !state;
    });
  };

  // —Ñ-—Ü–∏—è –ø–æ –¥–æ–±–∞–≤–∞–≤–ª–µ–Ω–∏—é 0 –µ—Å–ª–∏ –¥–ª–∏–Ω–∞ —á–∏—Ç—Å–ª–∞ 1
  const padTime = (time) => {
    return String(time).length === 1 ? `0${time}` : `${time}`;
  };
  // —Ñ–æ—Ä–º–∞—Ç–∏—Ä—É–µ—Ç –≤—Ä–µ–º—è –≤ MM:–°–°
  const format = (time) => {
    const minutes = Math.floor(time / 60);
    const seconds = time % 60;
    return `${minutes}:${padTime(seconds)}`;
  };
  // –æ–∫–æ–Ω—á–∞–Ω–∏–µ —Ç–∞–π–º–µ—Ä–∞ –∑–∞–ø–∏—Å—å –≤ –∏—Å—Ç–æ—Ä–∏—é

  useEffect(() => {
    if (count === 20) {
      if (mode === 'pomodoro') {
        dispatch(addHistoryPomodoro());
      } else if (mode === 'shortBreak') {
        dispatch(addHistoryShortBreak());
      } else if (mode === 'longBreak') {
        dispatch(addHistoryLongBreak());
      }
    }
    // eslint-disable-next-line
  }, [count]);

  useEffect(() => {
    let timer;
    if (count > 0 && timerActive) {
      timer = setTimeout(
        () =>
          setCount((c) => {
            return c - 1;
          }),
        1000,
      );
    }
    return () => {
      if (timer) {
        clearTimeout(timer);
      }
    };
  }, [count, timerActive]);

  //—Å—Ç–∏–ª–∏–∑–∞—Ü–∏—è –∫–Ω–æ–ø–∫–∏ —Å—Ç–∞—Ä—Ç let clazzTimerColor= 'timer';
  let clazzBtnStartColor = 'timer__btn_start';
  switch (mode) {
    case 'shortBreak':
      clazzBtnStartColor = 'timer__btn_start blue';
      break;
    case 'longBreak':
      clazzBtnStartColor = 'timer__btn_start navyBlue';
      break;
    default:
      clazzBtnStartColor = 'timer__btn_start';
  }

  return (
    <>
      <div className="timer__string">
        {count > 0 && timerActive ? format(count) : format(count)}
      </div>
      <button onClick={onToggleStart} className={clazzBtnStartColor}>
        {timerActive ? 'stop' : 'start'}
      </button>
    </>
  );
};

export default TimerCount;
