let initialState = {
  settings: {
    pomodoro: {
      name: 'pomodoro',
      time: 25,
    },
    shortBreak: {
      name: 'shortBreak',
      time: 5,
    },
    longBreak: {
      name: 'longBreak',
      time: 10,
    },
    autoPlay: false,
  },
  tasks: [
    {
      id: 3,
      text: '—Ö–µ–π –Ω–∞–∂–º–∏ –Ω–∞ —ç—Ç—É –∫–Ω–æ–ø–∫—É, —á—Ç–æ –±—ã —É–¥–∞–ª–∏—Ç—å —ç—Ç—É  –∑–∞–¥–∞—á—É ‚áí',
      numberPomodoro: 1,
      done: false,
    },
    { id: 0, text: '‚áê –ê —Ç–µ–ø–µ—Ä—å –Ω–∞–∂–º–∏ —Å—é–¥–∞ –µ—Å–ª–∏ —Å–¥–µ–ª–∞–ª –µ–µ', numberPomodoro: 2, done: false },
    {
      id: 2,
      text: '–ü—Ä–æ—à–ª–∞—è –∑–∞–¥–∞—á–∞ –∏—Å—á–µ–∑–ª–∞ –Ω–æ —Ç—ã –≤—Å–µ –µ—â–µ –º–æ–∂–µ—à—å –ø–æ—Å–º–æ—Ç—Ä–µ—Ç—å –µ–µ –Ω–∞–∂–∞–≤ –Ω–∞ –∫–Ω–æ–ø–∫—É  History, –æ–Ω–∞ –Ω–∞—Ö–æ–¥–∏—Ç—Å—è –Ω–∞ —Å–∞–º–æ–º –≤–µ—Ä—Ö—É ‚Üë',
      numberPomodoro: 4,
      done: false,
    },
    {
      id: 1,
      text: '—á—Ç–æ –±—ã –¥–æ–±–∞–≤–∏—Ç—å –∑–∞–¥–∞—á—É –Ω–∞–∂–º–∏ –Ω–∞ –∫–Ω–æ–ø–∫—É +Add task –∫–æ—Ç–æ—Ä–∞—è –Ω–∞—Ö–æ–¥–∏—Ç—Å—è –≤ —Å–∞–º–æ–º –Ω–∏–∑—É ü†ó',
      numberPomodoro: 4,
      done: false,
    },
  ],
  mode: 'pomodoro',
  modal: {
    history: {
      isOpened: false,
      title: 'History',
    },
    setting: {
      isOpened: false,
      title: 'Setting',
    },
    login: {
      isOpened: false,
      title: 'Login',
    },
  },
  history: {
    pomodoro: 0,
    shortBreak: 0,
    longBreak: 0,
  },
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case 'CHANGE-MODE': {
      return { ...state, mode: action.payload };
    }
    case 'MODAL-OPEN-HISTORY': {
      return {
        ...state,
        modal: { ...state.modal, history: { ...state.modal.history, isOpened: true } },
      };
    }
    case 'MODAL-OPEN-SETTING': {
      return {
        ...state,
        modal: { ...state.modal, setting: { ...state.modal.setting, isOpened: true } },
      };
    }
    case 'MODAL-CLOASE': {
      document.body.style.overflow = '';
      return {
        ...state,
        modal: {
          ...state.modal,
          setting: { ...state.modal.setting, isOpened: false },
          history: { ...state.modal.history, isOpened: false },
        },
      };
    }
    case 'CHANGE-TIME-MODAL': {
      return { ...state, settings: { ...action.payload } };
    }
    case 'ADD-HISTORY-POMODORO': {
      return { ...state, history: { ...state.history, pomodoro: state.history.pomodoro + 1 } };
    }
    case 'ADD-HISTORY-SHORT-BREAK': {
      return { ...state, history: { ...state.history, shortBreak: state.history.shortBreak + 1 } };
    }
    case 'ADD-HISTORY-LONG-BREAK': {
      return { ...state, history: { ...state.history, longBreak: state.history.longBreak + 1 } };
    }
    case 'CHANGE-TASK-DONE': {
      let newTasks = [...state.tasks];
      let index = newTasks.findIndex((el) => el.id === action.payload);
      newTasks[index].done = true;
      return { ...state, tasks: newTasks };
    }
    case 'DELETE-TASK': {
      let newTasks = state.tasks.filter((el) => el.id !== action.payload);
      return { ...state, tasks: newTasks };
    }
    case 'ADD-NEW-TASK': {
      let sortArr = [...state.tasks];
      const newId = sortArr.sort((a, b) => a.id - b.id).length;
      let newTask = { id: newId, text: action.payload, numberPomodoro: 1, done: false };
      return { ...state, tasks: [...state.tasks, newTask] };
    }
    default:
      return state;
  }
};

export default reducer;
